import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import AbstractFormCreator from './AbstractFormCreator';

class SearchPanel extends AbstractFormCreator {

    constructor( props ) {
        super(props);
        this.search = this.search.bind( this );
    }

    render() {
        let elements = this.props.items.map(item => {
            if ( item.type === "date" ) {
                return this.createDateInput( 12, 3 , "search", item );
            } else {
                return this.createTextInput( 12, 3, "search", item );
            }
        });

        return (
            <Row>
                <Col xs="12">
                    <Card>
                        <CardHeader></CardHeader>
                        <CardBody>
                            <Row>{elements}</Row>
                            <Row>
                                <Col xs="12" md="2">
                                    <Button type="submit" size="sm" color="primary" onClick={this.search}><i className="fa fa-search" aria-hidden="true"></i> </Button>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Col>
            </Row>
        )
    }

    search() {
        this.props.doSearch( this.refineProps() );
    }

}

export default SearchPanel;


